.authBtn{
    border: 4px solid rgba(112, 52, 3, 0.9);
    background-image: linear-gradient(rgba(250, 250, 250, 0.7), rgba(250, 250, 250, 0.7)),
                    linear-gradient(to right bottom, red,orange,yellow);
    background-clip: border-box;
}
.dark-authBtn{
    border-color: rgba(112, 52, 3, 0.9);
}

.authBtn i{
    opacity: 0.9;
}

.authBtn:hover{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
    border-color:rgba(0,0,0,1);
}

.authBtn:hover i{
    opacity: 0.3;
}

.authBtn:active{
    border-color: rgba(0,0,0,0.7);
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
                    linear-gradient(to right bottom, red,orange,yellow);
}

.authBtn:active i{
    color: whitesmoke;
    opacity: 0.3;
}

.authBtn:focus{
    outline: none;
}

i {
    vertical-align: middle;
}

.loginBtn{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
}

.loginBtn i{
    opacity: 0.9;
}

.loginBtn:hover i{
    opacity: 0.3;
}

@media only screen and (max-width: 768px) {
    .authBtn{
        border-width: 3px;
    }
}

@media only screen and (max-width: 576px) {
    .authBtn{
        border-width: 2px;
    }
}